<template>
  <div id="app">
    <!-- OTP -->
     <div v-if="!LoggedIn">
        <h1 style="font-family: emoji;font-size: 50px;">Login</h1>
        <br><br>
        <form @submit.prevent="submit">
            <div id="recaptcha-container" style="display: flex;justify-content: center;"></div>
            <br>
            <div>
              <vs-button id="log-in" type="submit" style="margin:auto;font-size:20px">Send OTP</vs-button>
            </div>
        </form>
        <br><br>        

        <br><br>
        <div v-if="smsSent" style="background-color:#1b1a1a;width:300px;height:auto;margin:auto; border-radius:10px;">
            <div style="background-color:black">
            <label class="EnterOTP">Enter Otp</label>
            </div>
            <br>
            <div style="display:flex;justify-content:center;">
                <vs-input type="text" 
                id="otpField"
                style="text-align:center"
                pattern="[0-9]{6}"
                maxlength="6"
                v-model="otpnum"
                placeholder="Enter OTP"
                required/>
            </div>
            <br>
            <div style="display:flex;justify-content:center;padding-bottom: 10px;">
              <vs-button class="btn btn-primary" @click="verifyCode" id="otp-btn">Confirm</vs-button>
            </div>
        </div>
    </div>
    <div v-else>
      <div class="topBar">
        <vs-button primary @click="active=!active" style="margin-left:25px;padding:3px">
          Upload Image
        </vs-button>
        <!-- <vs-button primary @click="sendMessage()" style="margin-left:25px;padding:3px">
          Send Msg
        </vs-button> -->

        <!-- Upload Image -->
        <vs-dialog prevent-close v-model="active">
          <template #header>
            <h4 class="not-margin">
              <b>PHOTO DRIVE</b>
            </h4>
          </template>

          <!-- <div class="con-form">
            <img src="" alt="Image" width="365">
          </div> -->

          <template #footer>
            <div class="footer-dialog">
              <vs-button @click="selectFile()" style="width:100%">
                Select Image
              </vs-button>
              <input type="file" id="upload_image" ref="imageData" style="display:none" multiple>
              <vs-button @click="uploadFile()" style="width:100%">
                Upload Image
              </vs-button>
            </div>
          </template>
        </vs-dialog>

        <!-- Delete Image -->
        <vs-dialog prevent-close v-model="active2">
          <template #header>
            <h4 class="not-margin">
              <h2>Warning</h2>
              <p>Are you sure you want to Delete this Image?</p>
            </h4>
          </template>

          <div class="con-form">
            <img :src="selectForDeletion" alt="" width="500px" class="LargeImage">
          </div>

          <template #footer>
            <div class="footer-dialog">
              <vs-button @click="active2=!active2,deleteImage()" style="width:100%">
                Yes
              </vs-button>
              <vs-button @click="active2=!active2" style="width:100%">
                No
              </vs-button>
            </div>
          </template>
        </vs-dialog>

        <!-- Edit Image -->
        <vs-dialog prevent-close v-model="editActive">
          <template #header>
            <h4 class="not-margin">
              <h2>Warning</h2>
              <p>Are you sure you want to Change this Image?</p>
            </h4>
          </template>

          <div class="con-form">
            <img :src="selectForEdit" alt="" width="500px" class="LargeImage">
          </div>

          <template #footer>
            <div class="footer-dialog">
              <vs-button @click="editImage()" style="width:100%">
                Choose File
              </vs-button>
              <input type="file" id="select_upload_image" ref="imageData" style="display:none">
              <vs-button @click="uploadEditFile()" style="width:100%">
                Upload Image
              </vs-button>
              <vs-button @click="editActive=!editActive" style="width:100%">
                Cancel Edit
              </vs-button>
            </div>
          </template>
        </vs-dialog>

        <!-- Preview Image -->
        <vs-dialog v-model="active3">
          <div class="con-form">
            <img :src="showLargeImage" alt="" width="800px" class="LargeImage">
          </div>

          <template #footer>
            <div class="footer-dialog">
              <p>{{showLargeImageName}}</p>
            </div>
          </template>
        </vs-dialog>

      </div>
      <h1 v-if="firestoreImages.length==0" class="NoDataExists">No Data exists!!!</h1>
      <div v-else>
          <div class="MainContainer" @scroll="handleScroll()">
              <div class="imageContainer" v-for="(data,index) in firestoreImages" :key="index">
                  <div class="options">
                      <vs-button
                          circle
                          icon
                          success
                          floating
                      >                    
                        <img src="https://freepikpsd.com/wp-content/uploads/2019/10/edit-png-file-4-Transparent-Images-Free.png" alt="edit" width="15px" class="optionIcons" @click="editActive=!editActive,editIndex=index,selectForEdit=firestoreImages[index].url" >
                        <i class='bx bxs-purchase-tag' ></i>
                      </vs-button>
                      <vs-button
                          circle
                          icon
                          danger
                          floating
                      >
                          <img src="https://visualpharm.com/assets/308/Trash%20Can-595b40b85ba036ed117dc053.svg" alt="edit" width="15px" class="optionIcons" @click="active2=!active2,deleteIndex=index,selectForDeletion=firestoreImages[index].url">
                          <i class='bx bxs-purchase-tag' ></i>
                      </vs-button>
                  </div>
                  <div class="ImageInternalContainer">
                      <img :src="data.url"  @click="active3=!active3,showLargeImage=firestoreImages[index].url,showLargeImageName=firestoreImages[index].name" alt="Image" width="365px" height="265px" class="image">
                      <p class="ImageName">{{data.name}}</p>
                  </div>
              </div>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
export default {
  name: 'App',
  data()
  {
    return{
      LoggedIn:false,

      active:false,
      selectForDeletion:null,
      selectForEdit:null,
      showLargeImage:null,
      showLargeImageName:null,
      deleteIndex:null,
      editIndex : null,
      active2:false,
      active3:false,
      editActive:false,
      docId:null,
      firestoreImages:[],
      Images:[],
      lastDoc:null,
      totalDocs:0,
      scrolled:false,
      Id:0,

      // OTP
      phoneNumber:'+919725374828', 
      confirmationResult:null,
      otpnum:null,
      recaptchaVerifier:null,
      recaptchaWidgetId:null,
      confirmResult:null,
      smsSent:false,
    }
  },
  created()
  {
    var self = this

    const db = firebase.firestore()

    db
      .collection("Images")
      .orderBy('id','asc')
      .get()
      .then(querySnapshot=>{
        if(!querySnapshot.empty)
        {
          self.totalDocs=querySnapshot.size+1
          self.firestoreImages=[]
          self.getImages()
        }
        else
        {
          console.log("No data exists!!")
        }
        console.log(querySnapshot)
        self.Id = querySnapshot.docs[querySnapshot.size-1].data().id+1
        console.log("LastId",self.Id)
      })
  },

  //For OTP ReCaptcha
  mounted()
  {
      firebase.auth().useDeviceLanguage()
      this.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('log-in',{
          'size':'invisible',
          'callback':(response) => {
          // reCAPTCHA solved, allow signInWithPhoneNumber.
          console.log(response)
          }
      })            
  },


  methods:
  {
    //Send OTP Message
    // sendMessage()
    // {
    //   console.log("send Msg")
    //   this.$axios.post('http://localhost:8000',{otp:2564})
    //   .catch(error=>{
    //     console.log(error)
    //   })
    // },

    handleScroll()
    {
      var container=document.querySelector('.MainContainer')
      var triggerHeight = Math.floor(container.scrollTop + container.clientHeight)
      // console.log(container.scrollHeight,triggerHeight)
      // console.log(`firestoreImages:${this.firestoreImages.length},Total:${this.totalDocs},Last:${this.lastDoc}`)

      if(container.scrollHeight == triggerHeight && this.firestoreImages.length < this.totalDocs && !this.scrolled)
      {
        // console.log("Scroll End!")
        this.scrolled=true
        this.getImages()
      }
      else if(container.scrollHeight-1 == triggerHeight && this.firestoreImages.length < this.totalDocs && !this.scrolled)
      {
        // console.log("Scroll End!")
        this.scrolled=true
        this.getImages()
      }
    },
    
    //Select Image/s
    selectFile()
    {
      document.getElementById('upload_image').click()
    },

    //Check Images for Upload
    uploadFile()
    {
      // alert("Upload File!")
      if(this.$refs.imageData.files.length==0)
      {
        alert("No files choosed!")
      }
      else
      {
        this.Images=this.$refs.imageData.files
        // console.log("Images:",this.Images)
        this.uploadImages()
        this.active=!this.active
      }
    },

    //Get Images
    getImages()
    {
      var self = this

      // self.firestoreImages=[]

      const db = firebase.firestore()

      db
          .collection("Images")
          .orderBy('id','asc')
          .startAt(self.lastDoc|| 0)
          .limit(15)
          .get()
          .then(querySnapshot=>{
            if(!querySnapshot.empty)
            {
              console.log(querySnapshot)
              self.docId = querySnapshot.docs[0].id

              querySnapshot.forEach((doc)=>{
                self.firestoreImages.push({url:doc.data().url,name:doc.data().name,id:doc.data().id,docId:doc.id})
              })
              // console.log(self.firestoreImages)
              self.lastDoc = this.firestoreImages.length
              self.scrolled=false
            }
            else
            {
              console.log("No data exists!!")
            }
          })
    },

    //Set Images
    addImages(loading,url,name,Id)
    {
      var self = this
      const db = firebase.firestore()

      db
        .collection("Images")
        .add({"url":url,"name":name,"id":Id})
        .then((Response)=>{
          // console.log("Firebase Response:",Response)
          self.docId=Response.id
          self.getImages()
          loading.close()
        })
        .catch(error=>{
          console.log(error)
        })
    },

    addMultipleImages(loading,url,name,Id)
    {
      var self = this
      const db = firebase.firestore()

      db
        .collection("Images")
        .add({"url":url,"name":name,"id":Id})
        .then((Response)=>{
          // console.log("Firebase Response:",Response)
          self.docId=Response.id
          loading.close()
        })
        .catch(error=>{
          console.log(error)
        })
    },

    //Upload Image
    uploadImages()
    {
      var self = this
      const storageRef = firebase.storage().ref()

      const loading = this.$vs.loading()
      var count = 0

      // ************************************** Upload MultipleImage **************************************
      if(self.Images.length>1)
      {
        self.Images.forEach((Img)=>{
          var uploadTask = storageRef.child("Images/"+Img.name).put(Img)

          setTimeout(()=>{
            // console.log("Ref:",self.Images)
  
            uploadTask.on('state changed',(snapshot)=>{
                var progress = (snapshot.bytesTransferred/snapshot.totalBytes)*100
  
                console.log(progress)
            },
            (error)=>{
                console.log(error)
            },
            ()=>{
          
                uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
                  // console.log('File available at', downloadURL);

                  self.addMultipleImages(loading,downloadURL,Img.name,self.Id)
                  self.Id++
                  count++

                  // console.log("Count:",count)
                  if(count==self.Images.length)
                  {
                    // console.log("Count:",count)
                    // console.log("FirestoreImages:",self.firestoreImages)
                    // self.addImages(loading,Id)
                    setTimeout(()=>{
                      self.getImages()
                      loading.close()
                    },1000*self.Images.length)
                  }
                });
            })
          },3000*self.Images.length)
        })
      }
      // ************************************** Upload SingleImage **************************************
      else
      {
        var Img = self.$refs.imageData.files[0]
        // console.log(Img)
        var uploadTask = storageRef.child("Images/"+Img.name).put(Img)

        uploadTask.on('state changed',(snapshot)=>{
            var progress = (snapshot.bytesTransferred/snapshot.totalBytes)*100
            console.log(progress)
        },
        (error)=>{
            console.log(error)
        },
        ()=>{
            uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
              // console.log('File available at', downloadURL);
              self.addImages(loading,downloadURL,Img.name,self.Id)
              self.Id++
          });
        })
      }
    
    },

    //Edit Image
    editImage()
    {
      document.getElementById('select_upload_image').click()
    },

    uploadEditFile()
    {
      var self = this

      console.log(self.firestoreImages[self.editIndex].docId,self.editIndex)

      if(this.$refs.imageData.files.length==0)
      {
        alert("No files choosed!")
      }
      else
      {
        const storageRef = firebase.storage().ref()

        const loading = this.$vs.loading()

        var Img = self.$refs.imageData.files[0]

        // console.log(Img)
        var uploadTask = storageRef.child("Images/"+Img.name).put(Img)
        

        uploadTask.on('state changed',(snapshot)=>{
            var progress = (snapshot.bytesTransferred/snapshot.totalBytes)*100
            console.log(progress)
        },
        (error)=>{
            console.log(error)
        },
        ()=>{
            uploadTask.snapshot.ref.getDownloadURL().then((downloadURL) => {
              // console.log('File available at', downloadURL);

              //Delete Previous Image
              storageRef.child("Images/"+self.firestoreImages[this.editIndex].name).delete()
              .catch(error=>{
                console.log(error)
              })

              const db = firebase.firestore()

              db
                .collection("Images")
                .doc(self.firestoreImages[self.editIndex].docId)
                .update({"url":downloadURL,"name":Img.name})
                .then((Response)=>{
                  console.log(Response)
                  self.firestoreImages=[]
                  self.getImages()
                  loading.close()
                })
                .catch(error=>{
                  console.log(error)
                })

              self.editActive = false
          });
        })
      }
    },

    //Delete Image
    deleteImage()
    {
      var self = this
      var loading = self.$vs.loading()
      console.log(self.deleteIndex,self.firestoreImages[self.deleteIndex],self.firestoreImages[self.deleteIndex].docId)
      
      const db = firebase.firestore()

      db
          .collection("Images")
          .doc(self.firestoreImages[self.deleteIndex].docId)
          .delete()
          .then(()=>{

            const storageRef = firebase.storage().ref()

            var DeleteImage = storageRef.child("Images/"+self.firestoreImages[self.deleteIndex].name)
            
            DeleteImage.delete()
            .then(()=>{
              console.log("Image Deleted.")
              self.firestoreImages.splice(self.deleteIndex,1)
              loading.close()
            })
            .catch(error=>{console.log(error)})
          })
          .catch(error=>{
            console.log(error)
          })
    },

    //OTP Verfication
    submit()
    {
      this.recaptchaVerifier = new firebase.auth.RecaptchaVerifier('recaptcha-container')
      this.recaptchaVerifier.render().then((widgetId)=>{
      this.recaptchaWidgetId = widgetId    
      })
      
      var number = this.phoneNumber            
      firebase.auth().signInWithPhoneNumber(number,this.recaptchaVerifier)
      .then((confirmationResult)=>{                
        this.confirmResult = confirmationResult
        console.log(this.confirmResult)
        alert("Sms Sent!")
        this.smsSent=true
      })
      .catch((error)=>{
        console.log("Sms not sent",error.message)
      })
    },
    verifyCode()
    {            
      this.confirmResult.confirm(this.otpnum)
      .then((result)=>{
        alert("Login Successfull!",result)
        this.LoggedIn=true
        var user = result.user
        console.log(user)
      })
      .catch((error)=>{
        console.log(error)
      })
    },
  }
}
</script>

